    .titulo-segundo.mt-5#t_3_1
      .h4(data-aos="fade-right") 3.1 Orden operacional de confeccin de camisa
    p.my-5 Para el desarrollo de la ruta operacional en confeccin de camisa se irn haciendo una serie de ejercicios con diferentes ejemplos trazos, cortes, formas, que al final permitirn el ensamble de la camisa.

    p En la siguiente tabla se presenta detalladamente el proceso para la preparacin de las piezas. 

    .tabla-b.color-acento-contenido.mt-5(data-aos="zoom-in")
      table
        caption Nota: Biblioteca SENA
        tr.bg-1
          td
          td Preparacin de las piezas
          td Proceso de plantillado fusionado y prehormado
        tr
          td
          td.tab-bold.br-tabble Preparacin de cuellos
          td 1. Fusionar el cuello centrando las entretelas sobre la tela del cuello, dejando 1cm de margen de costura por contorno.<br><br>2. Coser el contorno a  cm en la parte de arriba del cuello, rematando costura al iniciar y al terminar.<br><br>3. Despuntar, desbastar o perfilar, la parte de la punta del cuello para afinar el acabado y que al voltearlo guarde simetra.<br><br>4. Voltear el cuello y con la ayuda de las tijeras o con un punzn, definir las puntas cuidando no picar la pieza.<br><br>5. Juntar las puntas y verificar la simetra antes de pespuntear el cuello.<br><br>6. Pespuntar el filo del cuello. Esta costura o pespunte puede variar segn las especificaciones tcnicas, a 1/16 o 1/ 4. Rematar al inicio y al final del pespunte.
        tr
          td 1
          td.tab-bold.br-tabble Preparacin de banda o pie de cuello
          td 1. Planchar o fusionar la entretela al pie de cuello vista, por la parte de atrs emparejando de arriba.<br><br>2. Doblar la parte que qued sin entretela para hacer el dobladillo del pie de cuello.<br><br>3. Colocar el pie de cuello forro con la vista hacia arriba. <br><br>4. Colocar el cuello con la vista hacia arriba. Colocar el pie de cuello vista con la vista hacia abajo<br><br>5. Verificar que las 3 marcas del centro de las piezas coincidan, es decir, cuello con pie vista y pie forro. <br><br>6. Realizar costura del centro hacia las orillas, primero una curva y despus la otra, es decir, un lado y volteando el otro lado derecho.<br><br>7. Planchar la costura del pie del cuello y revisar que los 2 pies de cuello queden del mismo tamao y las 2 puntas del cuello. <br><br>8. Verificar el ancho de puntas y la coincidencia de pie de cuello.<br><br>9. Pespuntar al borde o filo del pie de cuello, el pespunte se realiza a 1 /16 o 1/4 o segn especificaciones tcnicas.
        tr
          td 2
          td.tab-bold.br-tabble Preparacin de la pechera, extensin de botonadura o cruce
          td 1. Doblar el delantero izquierdo(planchar) hacia el forro de la tela del lado de la lnea del ojal y botn, dejando una costura de 4 cm.<br><br>2. Segundo doblez, planchar nuevamente de 4 cm.<br><br>3. Realizar un pespunte de  cm de la orilla del segundo doblez, del lado del delantero. No olvidar rematar siempre al inicio y al final de cada costura.<br><br>4. Desdoblar el delantero y hagan una sobrecostura de  cm en la orilla del primer doblez, quedando formada la perilla.<br><br>5. Doblar por la parte de la lnea del ojal y botn, al delantero derecho, quedando a 2  cm terminado.<br><br>6. Primer doblez, planchar el delantero derecho y doblar hacia dentro 1 cm, o hacia el forro.<br><br>7. Segundo doblez, enseguida doblar por la lnea de las muescas 2  cm, quedando el dobladillo dentro de la camisa y la vista hacia arriba. Coser el delantero al filo por los 2  cm planchados con la vista hacia abajo, es decir, al filo del primer doblez para terminar el dobladillo.
        tr
          td 3
          td.tab-bold.br-tabble Preparacin y ensamble del bolsillo
          td 1. Doblar a 1 cm y doblar nuevamente a 2  cm, es decir, donde se hicieron las muescas en la parte de arriba del bolsillo.<br><br>2. Hacer un pespunte al orillo del dobladillo de 2  cm.<br><br>3. Pegar bolsillo prehormado en los piquetes de la parte delantera izquierda de la camisa, realizando un pespunte a 1 /16 al orillo del bolsillo y con remate en el inicio y final.
        tr
          td 4
          td.tab-bold.br-tabble Preparacin o ensamble de puo
          td 1.Fusionar los puos vista, posicionando la entretela con el pegamento por el revs de la tela de los puos vistas.<br><br>2. Dobladillar 1 cm la parte que qued sin entretela para hacer el dobladillo.<br><br>3. Colocar el puo forro con la vista hacia arriba.<br><br>4. Colocar el puo vista hacia abajo, encima del puo forro.<br><br>5. Coser el contorno del puo dejando  cm de pestaa en la parte de arriba.<br><br>Nota: Rematar al principio y al final de cada costura y coser de la misma manera el otro puo.<br><br>6. Si el puo es de puntas redondas, tengan mucho cuidado al pasar la costura para que no pierdan la forma.<br><br>7. Voltear los puos, revisar que las curvas de los puos queden iguales, un lado contra el otro.<br><br>8. Planchar los puos y pasar un pespunte, alrededor de la orilla de los puos para mejor acabado. Este pespunte puede variar segn las especificaciones tcnicas 1/16 o 1/4. Este pespunte regularmente es igual al del cuello.
        tr
          td 5
          td.tab-bold.br-tabble Preparacin de espalda
          td 1. Pegar marquilla si se tiene al centro de la almilla o canes forro por el derecho de la tela. Canes o almilla forro con la vista tela hacia arriba<br><br>2. Coser el pliegue o tabln de la espalda.<br><br>3. Doblar las dos marcas de la espalda.<br><br>4. Cuidar que coincidan con la marca o piquetes del medio, hacer una costura de  cm encima del tabln o pliegue o fuelle, en la parte alta de la espalda, no olvidar rematar al inicio y al final de la costura.<br><br>5. Colocar el forro de la bata con la vista hacia arriba, colocar la espalda con la vista hacia arriba, colocar la bata vista con la vista de la tela hacia abajo.<br><br>6. Juntar las 3 piezas para que queden parejas.<br><br>7. Unir con una costura dejando 1 cm de pestaa, rematando al inicio y al final.<br><br>8. Pespunte pasando la costura en la orilla del canes.
        tr
          td 6
          td.tab-bold.br-tabble Preparacin de portauela
          td 1. Doblar y planchar las portauelas a 1 cm de ambos lados de las aletillas hacia el revs de la tela.<br><br>2. Coser primero manga y portauela: Por uno de los lados planchados, pongan la manga con vista hacia abajo, y aletilla vista hacia abajo con una pestaa de 1 cm al pasar la costura por la y o marca.<br><br>3. Voltear la portauela y la manga y hagan sobrecostura, al filo de la aletilla, teniendo cuidado que la costura de abajo no se salga.<br><br>4. Encimar la aletilla para formar el pico, de tal manera que se identifique aletilla sobre aletilla. Debe quedar el pico de aletilla sobre aletilla para hacer el pespunte.<br><br>5. El pico de la portauela se forma con los dedos acomodando la parte de inferior con la parte superior se forma el pico.<br><br>6. Pasar pespunte al filo u orillo del pico de la portauela y continuar hasta los 3 cm hacia abajo.<br><br>7. Cruzar la aletilla y terminar el pespunte donde empezaron.                               

