          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10
              p Para la preparacin del cuello de la blusa camisera, se debe tener en cuenta algunas recomendaciones, que le permitirn un producto con mejores acabados, para esto le invito a ver el siguiente vdeo en donde encontrar de manera detallada todo el procedimiento de la confeccin del cuello.
              p En el siguiente vdeo podr revisar este paso a paso                
              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=jwt4avwp6y0&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=5" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Preparar cuello. 

      .row.justify-content-center.align-items-center(titulo="Paso 3") 
        .col-lg-10 
          p #[b Paso 3. Preparar manga coser portauela y puos:]           
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10 
              p Para la preparacin de la manga, requiere unos sencillos pasos que involucran piezas claves como el sesgo, la portauela y, los puos, recordando que estos ltimos debes estar previamente prehormado. 
              p En el siguiente vdeo podr revisar este paso a paso:                
              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=vxmsXHHBXWc&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=4" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Preparar manga, coser portapauelos y puos.               
      .row.justify-content-center.align-items-center(titulo="Paso 4") 
        .col-lg-10 
          p #[b Paso 4. Preparar delantero, coser pinzas y pespuntear pechera:]
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10 
              p La preparacin de los delanteros consiste en marcar previamente las pinzas, posteriormente coser.

              p La pechera deber estar previamente prehormada, y de esta forma a proceder a la unin de piezas, tal como se muestra en el paso a paso del siguiente 

              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=8zurivBBOsE&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=3" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Preparar delantero, coser pinzas y pespuntear pechera.

      .row.justify-content-center.align-items-center(titulo="Paso 5") 
        .col-lg-10 
          p #[b Paso 5. Preparar posterior, coser pinzas: ]        
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10
              p En el posterior las preparaciones del posterior consiste en coser las pinzas, para seguir paso a paso esta confeccin, te invito a ver el siguiente vdeo:                
              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=Vde63W5SJVA&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=12" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Preparar posterior, coser pinzas.                
      .row.justify-content-center.align-items-center(titulo="Paso 6") 
        .col-lg-10 
          p #[b Paso 6. Unir por hombros:  ]
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10 
              p El ensamble se inicia con la operacin de unir hombros, es importante tener en cuenta la alineacin de piezas. 
              p En el siguiente vdeo podr revisar este paso a paso:             
              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=gHQJfaPkahw&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=13" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Unir por hombros.

      .row.justify-content-center.align-items-center(titulo="Paso 7") 
        .col-lg-10 
          p #[b Paso 7. Pespuntar hombros:           ]
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10  
              p Se requiere de un pie 1/16 compensador, derecho, segn se indica en la firma tcnica de este procedimiento.
              p En el siguiente vdeo podr revisar este paso a paso:             
              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=0aF-nOKG1tA&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=17" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Pespuntar hombros.              
      .row.justify-content-center.align-items-center(titulo="Paso 8") 
        .col-lg-10 
          p #[b Paso 8. Pegar cuello:     ]
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10
              p Debe marcarse tenuemente alrededor del cuello, que nos indique en donde se deber pasar la puntada y pegar la pieza, teniendo especial cuidado en ser prolijo en la tarea y que coincidan con los medios de la pieza de destino.
              p En el siguiente vdeo podr revisar este paso a paso:             
              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=-r-B9BBPN4k&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=9" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Pegar cuello. 

      .row.justify-content-center.align-items-center(titulo="Paso 9") 
        .col-lg-10 
          p #[b Paso 9. Pespuntar cuello:          ]
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10 
              p La marquilla, se pegar en el centro del cuello, posteriormente se une la pieza, teniendo especial cuidado de ocultar los bordes de la costura dentro del cuello.
              p En el siguiente vdeo podr revisar este paso a paso:          

              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=-fYx-VI0_tE&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=10" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Pespuntar cuello.              
      .row.justify-content-center.align-items-center(titulo="Paso 10") 
        .col-lg-10 
          p #[b Paso 10. Pegar mangas:     ]
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10
              p La unin de las mangas en la blusa, requiere que se preste especial atencin en pegar derecho con derecho de la tela, para esto tenga en cuenta que debe de coincidir la parte del posterior de la manga (portauela), con el posterior de la blusa

              p En el siguiente vdeo podr revisar este paso a paso:             

              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=qOpxd29hxko&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=8" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Pegar mangas. 

      .row.justify-content-center.align-items-center(titulo="Paso 11") 
        .col-lg-10 
          p #[b Paso 11. Pespuntar mangas:           ]
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10
              p Para iniciar el pespunte de las mangas, se debe tumbar hacia el cuerpo de la blusa, y luego usando el pie compensado derecho, se da inicio al pespunte con remate al inicio y respetando la curva que tiene la manga. 

              p Es un procedimiento sencillo, y que requiere atencin, para una mejor explicacin le invitamos a revisar el siguiente paso a paso en el vdeo:
              
              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=L2Tv-blhRnM&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=14" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Pespuntar mangas.               
      .row.justify-content-center.align-items-center(titulo="Paso 12") 
        .col-lg-10 
          p #[b Paso 12. Cerrar costados:     ]
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10
              p La unin por costados de la blusa, debe tenerse en cuenta que el derecho filete debe quedar por el delantero, se alinean las piezas y se revisa que coincidan los piquetes de costura. Este paso requiere especial atencin a los detalles.
              p En el siguiente vdeo podr encontrar una amplia explicacin al respecto.

              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=gOtAib9rMwQ&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=11" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Cerrar costados.

      .row.justify-content-center.align-items-center(titulo="Paso 13") 
        .col-lg-10 
          p #[b Paso 13. Pegar puos:           ]
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10 
              p Con el puo previamente preparado, vamos a pegarlo a la manga por la marcacin que se ha realizado, teniendo en cuenta que no se recoja la tela. 
              p En el siguiente vdeo podr revisar este paso a paso:             

              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=budtB_UPjtU&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=15" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Pegar puos.                
      .row.justify-content-center.align-items-center(titulo="Paso 14") 
        .col-lg-10 
          p #[b Paso 14. Pespuntar puo:     ]
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10
              p Pespuntar el puo, se inicia empatando la costura en donde se haba anteriormente despuntado el puo (paso 13), para esto se requiere el pie de pespunte compensado o de gua fija de 1/16. Tal y como se indica en el vdeo del paso a paso que encuentra a continuacin:                
              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=xnaxcqqKfTw&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=16" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Pespuntar puo. 

      .row.justify-content-center.align-items-center(titulo="Paso 15") 
        .col-lg-10 
          p #[b Paso 15. Dobladillar ruedo:           ]
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10 
              p En el ensamble, dobladillar el ruedo se utiliza el pie compensado de 1/16 lado derecho.

              p Se dobladilla doble vez un centmetro, de esta forma se inicia a coser, tal como se indica en el paso a paso del vdeo que encuentra a continuacin:
            
              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=pTroSaIBKDs&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=6" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Dobladillar ruedo.                
      .row.justify-content-center.align-items-center(titulo="Paso 16") 
        .col-lg-10 
          p #[b Paso 16. Marcar ojales y ojalar:     ]
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10 
              p Las operaciones de terminado se inicia con la marcacin de los ojales. Para esto hacemos uso de la plantilla, de esta forma marcar e iniciar el ciclo del ojal.
              p En el siguiente vdeo podr revisar este paso a paso:                
              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=LemTAGq3BwI&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=7" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Marcar ojales y ojalar. 

      .row.justify-content-center.align-items-center(titulo="Paso 17") 
        .col-lg-10 
          p #[b Paso 17. Marcar botones y botonar:           ]
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10 
              p Antes de pegar los botones es necesario marcar el lugar exacto en donde ir el botn, para esto es necesario ayudarnos de los ojales, tanto en el cuerpo de la camisa como en los puos de esta.
              p El vdeo que encuentra a continuacin explica de forma clara el paso a paso para pegar los botones.               
              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=Zwx_QULWajg&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=18" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Marcar botones y botonar.                
      .row.justify-content-center.align-items-center(titulo="Paso 18") 
        .col-lg-10 
          p #[b Paso 18. Limpiar, revisar y planchar:     ]
          .row.justify-content-center.align-items-center.mt-3
            .col-lg-10 
              p La ltimas operaciones consisten en pulir y limpiar nuestra prenda, retirando hebras o cortando de raz aquellas que hayan quedado, posteriormente planchar y empacar.
              p En el siguiente vdeo podr revisar este paso a paso:               
              a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=azhymYdHJBw&list=PLkc5n6npRWkink2WtHYTQCrNkLrWX2L12&index=19" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-link.svg")
                .anexo__texto
                  p #[b Enlace web]. Limpiar, revisar y planchar.
    hr
