    .row.justify-content-center.align-items-stretch
      .col-lg-6.text-just.cajon.color-acento-contenido.p-5.img-border-y
        p.mb-0 Un polo es una prenda de punto para el tronco que tiene la misma forma que una camiseta o playera, pero adems tiene cuello, llegando tambin hasta la cintura y que puede tener manga larga o corta. Tpicamente tiene una abertura delantera con dos o tres botones para ajustar el cuello y puede tener un bolsillo e incluso sustituir los botones por una cremallera. Esta fue ideada en el siglo XX para uniformar a los equipos que practicaban deporte.
