    LineaTiempoC.color-acento-contenido.mt-5
      .row.justify-content-center.align-items-center(titulo="Paso 1")
        .col-lg-10
          h5 Alistamiento en la confeccin de la camiseta tipo polo
          p Para iniciar la confeccin de la camiseta tipo polo, debers tener a mano lo correspondiente a:
          .row.justify-content-center.align-items-stretch
            .col-lg-5
              .list.mt-2
                  .list__item
                      .list__item__number
                          span 1.
                  .list__txt 
                      span Ficha tcnica.
              .list.mt-2
                  .list__item
                      .list__item__number
                          span 2.
                  .list__txt 
                      span Muestra fsica.
              .list.mt-2
                  .list__item
                      .list__item__number
                          span 3.
                  .list__txt 
                      span El paquete de patrones.
              .list.mt-2
                  .list__item
                      .list__item__number
                          span 4.
                  .list__txt 
                      span Las herramientas y utensilios de trabajo, incluidos los pies de la mquina y la gua dobladilladora, para llevar a cabo la confeccin.
            .col-lg-5                      
              .list.mt-2
                  .list__item
                      .list__item__number
                          span 5.
                  .list__txt 
                      span Material para la confeccin, previamente cortado.
              .list.mt-2
                  .list__item
                      .list__item__number
                          span 6.
                  .list__txt 
                      span Insumos como: hiladillo, botones, tallas y marquillas, puos y cuello tejidos, y por ltimo la hilaza
            
          p.mt-4 En el siguiente vdeo podr revisar este paso a paso
          a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=wsb9glAu1qk&list=PLkc5n6npRWkjH9BnFJlUpDKgV0Zk3yhVe&index=12" target="_blank")
            .anexo__icono
              img(src="@/assets/template/icono-link.svg")
            .anexo__texto
              p #[b Enlace web]. Alistamiento para la confeccin de la camiseta tipo polo.               
      .row.justify-content-center.align-items-center(titulo="Paso 2")
        .col-lg-10
          h5 Prehormar pechera y hacer quiebre en delantero
          p La preparacin de este paso inicia haciendo quiebre por el centro frente, en donde se tiene el delantero doblado, esto se hace para ubicar la pechera, la cual debe estar previamente fusionada, con el fin de tener un producto ms prolijo, se usa una plantilla, para hacer el quiebre del doblez y as proceder a dobladillar la pechera.
          p En el siguiente vdeo podr revisar este paso a paso de forma detallada
          a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=v_yemOgg9Rw&list=PLkc5n6npRWkjH9BnFJlUpDKgV0Zk3yhVe&index=11" target="_blank")
            .anexo__icono
              img(src="@/assets/template/icono-link.svg")
            .anexo__texto
              p #[b Enlace web]. Prehormar pechera y hacer quiebre en delantero. 

      .row.justify-content-center.align-items-center(titulo="Paso 3")
        .col-lg-10
          h5 Pegar pechera 
          p La confeccin de la pechera, debe contar con una gua tcnica que especifique el tipo de camiseta y modelo.
          p En la parte del patronaje, se indican las piezas de confeccin, es necesario revisar que estas vengan completas, as como sus medidas, con el fin de verificar tallas.
          p Tambin es necesario contar con la ficha tcnica de insumos y confeccin, en donde se indique todos los requerimientos de hilos e insumos. Y por ltimo algo muy importante, que son las indicaciones de confeccin con la ruta operacional de la prenda, es decir, el paso a paso.
          p Esta gua tcnica, tambin incluye las condiciones de calidad en cada una de las operaciones de confeccin.          
          p En el siguiente vdeo podr revisar el paso a paso para pegar la pechera
              
          a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=5B1k2XznFlg&list=PLkc5n6npRWkjH9BnFJlUpDKgV0Zk3yhVe&index=20" target="_blank")
            .anexo__icono
              img(src="@/assets/template/icono-link.svg")
            .anexo__texto
              p #[b Enlace web]. Pegar pechera.               
      .row.justify-content-center.align-items-center(titulo="Paso 4")
        .col-lg-10
          h5 Unir por hombros
          p La unin de la prenda por hombros, inicia al colocar las piezas de enfrentando derecho con derecho, de tal forma que queden alineadas y se procede a coser por el orillo.
          p La forma de llevar a cabo este procedimiento podr ser visualizada en el siguiente vdeo
              
          a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=rBrE6bikCRw&list=PLkc5n6npRWkjH9BnFJlUpDKgV0Zk3yhVe&index=10" target="_blank")
            .anexo__icono
              img(src="@/assets/template/icono-link.svg")
            .anexo__texto
              p #[b Enlace web]. Unir por hombros.

      .row.justify-content-center.align-items-center(titulo="Paso 5")
        .col-lg-10
          h5 Pegar puos tejidos a mangas
          p Para realizar la operacin de pegar puos tejidos a mangas, debe tenerse en cuenta que se debe alinear la tela, y despus de colocar el puo, se ayuda del prensatela para iniciar la costura.
          p Es importante tener cuidado la alineacin de las telas, tanto al principio como al final de la manga, evitando as que se corran estas.
          p En el siguiente vdeo, podr apreciar el paso a paso de esta operacin
              
          a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=WiAEkGtqQG4&list=PLkc5n6npRWkjH9BnFJlUpDKgV0Zk3yhVe&index=9" target="_blank")
            .anexo__icono
              img(src="@/assets/template/icono-link.svg")
            .anexo__texto
              p #[b Enlace web]. Pegar puos tejidos a mangas.                
      .row.justify-content-center.align-items-center(titulo="Paso 6")
        .col-lg-10
          h5 Pespuntar puos de mangas
          p Esta operacin se realiza por el lado derecho y con ayuda del pie prensatela especial de  con prensado derecho.
          p Es un proceso fcil de realizar, y en el siguiente vdeo podr revisar este paso a paso
              
          a.anexo.mt-4.mb-lg-0(href="https://www.youtube.com/watch?v=HBleW0yTvAg&list=PLkc5n6npRWkjH9BnFJlUpDKgV0Zk3yhVe&index=8" target="_blank")
            .anexo__icono
              img(src="@/assets/template/icono-link.svg")
            .anexo__texto
              p #[b Enlace web]. Pespuntar puos de mangas.

      .row.justify-content-center.align-items-center(titulo="Paso 7")
        .col-lg-10
          h5 Pespuntar hombros
