        .col-lg-10
          h5 fijar puntas de cuello
          p Para llevar a cabo esta operacin, primero se debe refilar las puntas que tienen los cuellos tejidos, procediendo a pegar desde la pechera, fijndola a 7 milmetros, solo la punta (medio centmetro aproximadamente).
